import { faHatCowboySide } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import "./style.css";
import NavigationBar from "../../component/navBar/unAuthenticatedNavBar";
import { useState } from "react";
import axios from "axios";
import { Link } from 'react-router-dom';

const SignUp = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [phone, setPhone] = useState("");
  const [name, setName] = useState("");
  const [location, setLocation] = useState("");
  const [country, setCountry] = useState("");

  const setSignUp = (credentials) => {
    axios
      .post("https://kagehr.herokuapp.com/api/company/signup", JSON.stringify(credentials))
      .then((data) => data.data)
      .then((data) =>
        data.success === true ? console.log("Success") : console.log("Failed")
      )
      .finally(alert("Opps! Still at Beta, Login with \n email: 'test@mail.com' \n password:'1234'"))
      ;
  };

  async function getSignUp(e) {
    e.preventDefault();
    await setSignUp({
      email,
      password,
      name,
      phone,
      location,
      country,
    });
  }

  return (
    <>
      <NavigationBar />
      <div className="login_container">
        <h1>
          <FontAwesomeIcon icon={faHatCowboySide} color="#3ec78e" /> Kage-HR
        </h1>

        <div>
          <h5>Create a new profile</h5>
          <br />
          <h6>
            Have an account? <span><Link to='/login'>Login</Link></span>{" "}
          </h6>
        </div>

        <form>
          <label>Name</label>
          <input onInput={(e) => setName(e.target.value)} type="text" />
          <label>Email</label>
          <input onInput={(e) => setEmail(e.target.value)} type="email" />
          <label>Password</label>
          <input onInput={(e) => setPassword(e.target.value)} type="password" />
          <label>Phone</label>
          <input onInput={(e) => setPhone(e.target.value)} type="text" />
          <label>Location</label>
          <input onInput={(e) => setLocation(e.target.value)} type="text" />
          <label>Country</label>
          <input onInput={(e) => setCountry(e.target.value)} type="text" />
          <button onClick={getSignUp}>Sign up</button>
        </form>
        <div></div>
      </div>
    </>
  );
};

export default SignUp;
