import React, { Component } from "react";
import {  Route, Switch } from "react-router-dom";
// import Dashboard from "./routes/DashBoardComponent/Dashboard";
 import Login from "./routes/AuthenticationComponent/Login";
 import SignUp from "./routes/AuthenticationComponent/SignUp"
 import Candidates from "./routes/CandidatesComponent/Candidates";
 import Job from "./routes/JobComponent/JobComponent";
 import Profile from "./routes/ProfileComponent/ProfileComponent"
class App extends Component {
  state = {};
  render() {
    return (
      
      <Switch>
        {/* <Route path="/" component={Dashboard} exact /> */}
        <Route path="/login" component={Login} />
        <Route path="/signup" component={SignUp} />
        <Route path="/candidates" component={Candidates} />
        <Route path="/profile" component={Profile} /> 
        <Route path="/" component={Job} exact/> 
        
      </Switch>
    );
  }
}

export default App;
