import {atom} from 'recoil';

const localStorageEffect = key =>({setSelf, onSet}) =>{
    const savedValue = localStorage.getItem(key)
    if (savedValue != null) {
      setSelf(savedValue)
    }
    onSet(newValue => {
      localStorage.setItem(key, newValue);
    });
  };

const user = atom({
    key:'user',
    default:{},
    effects_UNSTABLE: [
        localStorageEffect('user'),
    ],
});

export default user;