import React, {useState, useEffect} from 'react';
import "./style.css";
import NavigationBar from "../../component/navBar/navigationComponent";
import SASSERY from '../../asset/logo.png'
import axios from 'axios';
import user from "../../atom";
import { useRecoilValue } from "recoil";




const Profile=()=>{
    const userID = useRecoilValue(user);
    const [Profile, setProfile] = useState([{
        name:"",
        location:"",
        country:""
    }]);

    const getProfile = () => {
        axios
          .get(`https://kagehr.herokuapp.com/api/company/?_id=${userID}`)
          .then((response) => response.data)
          .then((response) => {setProfile(response._id)
           
          });
      };

      useEffect(() => {
        getProfile();
        

      }, []);

    return(
        <>
        <NavigationBar/>
        
        <div className="profile_container">
<div className="left_content">
<div className="content">
<h6>My Profile</h6>
<div className="profile_x">
    <div className="profile_img"><img alt='icon' src={SASSERY} /></div>
    <div className="profile_actions" >
        <span>UPLOAD LOGO</span>
        <input id="upload" type="file" />
    </div>
</div>

<div className="action_navigation">
<ul>
    <li className="active">
        <span>
            GENERAL INFORMATION
        </span>
        <span>
            PLEASE FILL ALL DETAILS
        </span>
    </li>

    <li>
        <span>
            BEGIN VERIFICATION
        </span>
        <span>
            VERIFY YOU COMPANY
        </span>
    </li>

    <li>
        <span>
            CHANGE PASSWORD
        </span>
        <span>
            CREATE A NEW PASSWORD
        </span>
    </li>
</ul>
</div>
</div>



</div>
<div className="right_content">
<div className="general_form_container">
<h6>GENERAL INFORMATION</h6>

<form>
<input value={Profile[0].name} placeholder="Name"/> 
<div  className='input_flex'><input value={Profile[0].email} placeholder="Email"/><input value={Profile[0].phone} placeholder="Phone"/> </div>
<div className='input_flex'><input value={Profile[0].social===null?"":Profile[0].social} placeholder="Social Media"/><input value={Profile[0].website===null?"":Profile[0].website} placeholder="Website"/></div>
<div className='input_flex'><input value={Profile[0].location===null?"":Profile[0].location.toUpperCase()} placeholder="Location"/><input value={Profile[0].country===null?"":Profile[0].country.toUpperCase()} placeholder="Country"/></div>
<textarea value={Profile[0].about===null?"":Profile[0].about} placeholder="Tell Applicants About Your Company..."/>
<div className='verify_status'><span>Status</span>{Profile[0].verified===true?<span>Verified</span>:<span style={{color:"rgb(255, 51, 51)"}}>Unverified</span>}</div>

<button>SAVE ALL CHANGES</button>
</form>
</div>
</div>

        </div>
        </>
    )
}

export default Profile;