import { faHatCowboySide } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React from 'react';
import './style.css';
import NavigationBar from '../../component/navBar/unAuthenticatedNavBar';
import { useState } from 'react';
import axios from 'axios';
import { useRecoilState } from 'recoil';
import user from '../../atom';
import { Link } from 'react-router-dom';


const  Login=()=>{
    const [email, setEmail]=useState('')
    const [password, setPassword] =useState('')
    const [currentUser, setCurrentUser] = useRecoilState(user)


const getLogin=(credentials)=>{
    axios.post('https://kagehr.herokuapp.com/api/company/login',credentials)
    .then(data=> data.data)
    .then(data=>data.success===true ?setCurrentUser(data.id):console.log("Failed"))
}


async function setLogin(e){
    e.preventDefault();
  await  getLogin({
        email,password
    })
    window.location = '/'
}

    return(
        <>
        <NavigationBar/>
        <div className='login_container'>
        <h1><FontAwesomeIcon icon={faHatCowboySide} color='#3ec78e'/> Kage-HR</h1>
        <div>
            <h5>Sign in to your account</h5>
            <br/><h6>Don't have an account? <span><Link to='/signup'>Start free trial</Link></span> </h6>
        </div>

        <form>
        <label>Email address</label><input onInput={(e)=>setEmail(e.target.value)} type='email' />
        <label>Password</label><input onInput={(e)=>setPassword(e.target.value)} type='password'/>
        <button onClick={setLogin}>Sign in</button>
        </form>
<div>

</div>
        </div>
        </>
    )
}

export default Login;