import React, {useState, useEffect,useRef} from 'react';
import NavigationBar from '../../component/navBar/navigationComponent';
import CandidateCard from '../../component/cards/candidatecard/candidateCard';
import './style.css';
import axios from 'axios';
import user from "../../atom";
import { useRecoilValue } from "recoil";
import moment from 'moment';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFilter } from '@fortawesome/free-solid-svg-icons';



const Candidates=(props)=>{

    const [Candidate, setAllCandidates] = useState([]);
    const [Filter, setFilter] = useState([]);
    const userID = useRecoilValue(user);
    const detailCard = useRef(null);
    const hint_detail = useRef(null);


    const getCandidates = () => {
        axios
          .get(`https://kagehr.herokuapp.com/api/employee?employer=${userID}`)
          .then((response) => response.data)
          .then((response) => {setAllCandidates(response)
          });
      };

      const getCandidateDetail = (id)=>{
          detailCard.current.style.display="none";
          hint_detail.current.style.display="none";
          detailCard.current.style.display="block";
          axios.get(`https://kagehr.herokuapp.com/api/employee?employer=${userID}&_id=${id}`)
          .then((response) => response.data)
          .then((response) => {
            detailCard.current.children[0].children[0].children[1].children[0].innerHTML= `${response[0].interview===true?'In Progress':'Interview'}`
            detailCard.current.children[0].children[0].children[1].children[1].innerHTML= `${response[0].rejected===true?'Rejected':'Reject'}`
            detailCard.current.children[0].children[0].children[0].innerHTML=`${response[0].firstname} ${response[0].lastname}`
            detailCard.current.children[3].children[1].innerHTML=response[0].phone
            detailCard.current.children[4].children[1].innerHTML=response[0].email
            detailCard.current.children[5].children[1].innerHTML=response[0].location
            detailCard.current.children[6].children[1].innerHTML=response[0].social
            detailCard.current.children[7].children[1].innerHTML=response[0].website
            });

        
        };


        let credentials = {};
        const setStatusPost = () => {
          axios
            .post(
              "https://kagehr.herokuapp.com/api/job/create",
              JSON.stringify(credentials),
              { headers: { "content-type": "application/json" } }
            )
            .then((response) => response.data)
            .then((response) => alert("Created Successfully"))
            .catch((err) => alert("Oops! an error occurred"));
        };
    
      useEffect(() => {
        getCandidates();
        

      }, []);
    

     


    return(
        <>
        <NavigationBar/> 
        <br/>
        <br/>
        <br/>
        <br/>
        <div className='emphasis_candidate'>Candidates</div>
        <section id='column_head'>
        <div id='left_side'><span><FontAwesomeIcon icon={faFilter}/></span>
        <select onInput={(e)=> setFilter(e.target.value|| '')}>
        <option value='position'>Role</option>
        <option value='code'>Code</option>
        <option value='location'>Location</option>
        </select>
        
        <select>
        {/* Work on making it render one value having the same state */}
        {Candidate.map(target=><option  value={target[Filter]}>
        { 
          target[Filter]
          }
          </option>)}
        </select>
        
        </div>
        <div id='right_side'><span>Candidate details</span></div>
        </section>
        <section id='column'>
        <div id='left_side'>
        { Candidate.length > 0 ? Candidate.map(value=>(<CandidateCard   key={value._id} name={`${value.firstname} ${value.lastname}`} role='Software Developer' time={moment(`${value.createdAt}`).fromNow()} status={`${value.interview && value.hired === true ?'success': value.rejected  === true? 'reject': value.interview===true? 'progress': 'new'}`} click={()=>getCandidateDetail(value._id)} />)): <span className='empty'>Opps! No Candidates Yet...</span>}</div>

        {/* Candidate Detail */}
        <div ref={hint_detail} className='empty_detail'> Click card to see detail...</div>
        <div ref={detailCard} id='right_side'>
        <div className='candidate_head'>
        <ul><li>Arash Hussein</li>
        <li>
            <div>Interview</div>
            <div>Reject</div>
        </li>
        </ul>
        </div> 
{/* End of head */}
        
        <div id='candidate_card_detail'> General Information</div>
        <div id='candidate_card_detail'> <span>Role :</span><span> UI/UX Designer</span></div>
        <div id='candidate_card_detail'> <span>Phone :</span><span> +2348112806410</span></div>
        <div id='candidate_card_detail'> <span>Email :</span><span> test@mail.com</span></div>
        <div id='candidate_card_detail'> <span>Location :</span><span> Lagos</span></div>
        <div id='candidate_card_detail'> <span>Social :</span><span> Visit </span></div>
        <div id='candidate_card_detail'> <span>Website :</span><span> https://www.portfolio.io</span></div>
        </div>
        </section>


        </>
    )
}

export default Candidates;